	var rules = "<h3>\u041E\u043F\u0438\u0441\u0430\u043D\u0438\u0435:</h3>\n<p>\u0420\u0435\u0432\u044C\u044E \u0431\u0443\u0434\u0435\u0442 \u043F\u0440\u043E\u0432\u043E\u0434\u0438\u0442\u044C\u0441\u044F \u043D\u0430 \u043E\u0441\u043D\u043E\u0432\u0430\u043D\u0438\u0438 \u043E\u0446\u0435\u043D\u043A\u0438 \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A\u0430\u043C\u0438 \u0434\u0440\u0443\u0433 \u0434\u0440\u0443\u0433\u0430.</p>\n<p>\u041D\u0430\u0432\u044B\u043A\u0438 \u043F\u043E \u043A\u043E\u0442\u043E\u0440\u044B\u043C \u0431\u0443\u0434\u0435\u0442 \u043F\u0440\u043E\u0432\u043E\u0434\u0438\u0442\u044C\u0441\u044F \u043E\u0446\u0435\u043D\u043A\u0430:</p>\n<ul>\n  <li><b>Hard Programming Skills</b> (OOP, Python, Django, Javascript, Angular, \u0432 \u0437\u0430\u0432\u0438\u0441\u0438\u043C\u043E\u0441\u0442\u0438 \u043E\u0442 \u043F\u043E\u0437\u0438\u0446\u0438\u0438)</li>\n  <li><b>Hard Operation Skills</b> (Unix, Git, Jira, etc)</li>\n  <li><b>Communication</b> (\u043A\u0430\u0447\u0435\u0441\u0442\u0432\u043E \u043E\u0431\u0441\u0443\u0436\u0434\u0435\u043D\u0438\u0439, \u0441\u0432\u043E\u0435\u0432\u0440\u0435\u043C\u0435\u043D\u043D\u044B\u0435 \u043E\u0442\u0432\u0435\u0442\u044B, \u0434\u043E\u0445\u043E\u0434\u0447\u0438\u0432\u043E\u0441\u0442\u044C \u0438\u0437\u043B\u0430\u0433\u0430\u0435\u043C\u043E\u0439 \u043C\u044B\u0441\u043B\u0438, \u0442\u0430\u043A\u0442\u0438\u0447\u043D\u043E\u0441\u0442\u044C)</li>\n  <li><b>Understanding</b> (\u043D\u0430\u0441\u043A\u043E\u043B\u044C\u043A\u043E \u0431\u044B\u0441\u0442\u0440\u043E \u043F\u0440\u043E\u0438\u0441\u0445\u043E\u0434\u0438\u0442 \u043E\u0431\u0443\u0447\u0435\u043D\u0438\u0435, \u043E\u0441\u043E\u0437\u043D\u0430\u0432\u0430\u043D\u0438\u0435 \u0441\u043F\u0435\u0446\u0438\u0444\u0438\u043A\u0438 \u043F\u0440\u043E\u0435\u043A\u0442\u0430, \u043F\u043E\u043D\u0438\u043C\u0430\u043D\u0438\u0435 \u043E\u0431\u0449\u0438\u0445 \u043F\u0440\u043E\u0446\u0435\u0441\u0441\u043E\u0432, \u0432\u043E\u0432\u043B\u0435\u0447\u0435\u043D\u043D\u043E\u0441\u0442\u044C \u0432 \u043A\u043E\u043C\u0430\u043D\u0434\u043D\u0443\u044E \u0440\u0430\u0431\u043E\u0442\u0443)</li>\n  <li><b>Self-sufficiency</b> (\u0442\u0440\u0435\u0431\u0443\u0435\u0442 \u043B\u0438 \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A \u0438\u0437\u043B\u0438\u0448\u043D\u0435\u0433\u043E \u043F\u043E\u0441\u0442\u043E\u0440\u043E\u043D\u043D\u0435\u0433\u043E \u0432\u043D\u0438\u043C\u0430\u043D\u0438\u044F \u0438 \u043F\u043E\u043C\u043E\u0449\u0438 \u043F\u0440\u0438  \u0432\u044B\u043F\u043E\u043B\u043D\u0435\u043D\u0438\u0438 \u0437\u0430\u0434\u0430\u0447 \u0438\u043B\u0438 \u043D\u0430\u043E\u0431\u043E\u0440\u043E\u0442 \u0441\u0442\u0440\u0435\u043C\u0438\u0442\u0441\u044F \u043F\u043E\u043C\u043E\u0447\u044C \u043A\u043E\u043B\u043B\u0435\u0433\u0430\u043C)</li>\n  <li><b>Focus & Motivation</b> (\u0437\u0430\u0438\u043D\u0442\u0435\u0440\u0435\u0441\u043E\u0432\u0430\u043D\u043D\u043E\u0441\u0442\u044C \u0432 \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E\u043C \u0440\u0435\u0437\u0443\u043B\u044C\u0442\u0430\u0442\u0435, \u0438\u0437\u0443\u0447\u0435\u043D\u0438\u0435 \u043D\u043E\u0432\u043E\u0433\u043E, \u0444\u043E\u043A\u0443\u0441 \u043D\u0430 \u043F\u0440\u043E\u0446\u0435\u0441\u0441\u0435)</li>\n  <li><b>English</b></li>\n</ul>\n<p>\u0410\u043D\u0433\u043B\u0438\u0439\u0441\u043A\u0438\u0439 \u044F\u0437\u044B\u043A \u043E\u0446\u0435\u043D\u0438\u0432\u0430\u0435\u0442 \u043F\u0440\u0435\u043F\u043E\u0434\u0430\u0432\u0430\u0442\u0435\u043B\u044C \u0432 \u0445\u043E\u0434\u0435 \u043A\u043E\u0440\u043E\u0442\u043A\u043E\u0433\u043E \u0438\u043D\u0442\u0435\u0440\u0432\u044C\u044E.</p>\n<p>\u0420\u0430\u0437\u0440\u0430\u0431\u043E\u0442\u0447\u0438\u043A\u0438 \u043E\u0446\u0435\u043D\u0438\u0432\u0430\u044E\u0442\u0441\u044F \u0434\u0440\u0443\u0433\u0438\u043C\u0438 \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A\u0430\u043C\u0438 \u043F\u043E \u043F\u0435\u0440\u0432\u044B\u043C \u0448\u0435\u0441\u0442\u0438 \u043F\u0443\u043D\u043A\u0442\u0430\u043C.</p>\n<p>\u041C\u0435\u043D\u0435\u0434\u0436\u0435\u0440\u044B, \u0442\u0435\u0441\u0442\u0438\u0440\u043E\u0432\u0449\u0438\u043A\u0438, \u0434\u0438\u0437\u0430\u0439\u043D\u0435\u0440\u044B, DevOps <b>\u043D\u0435 \u043E\u0446\u0435\u043D\u0438\u0432\u0430\u044E\u0442\u0441\u044F</b> \u043F\u043E \u0448\u043A\u0430\u043B\u0435 operation skills, \u0430 \u043F\u0435\u0440\u0432\u0430\u044F \u0448\u043A\u0430\u043B\u0430 (programming skills) \u0434\u043B\u044F \u043D\u0438\u0445 \u0431\u0443\u0434\u0435\u0442 \u043E\u0437\u043D\u0430\u0447\u0430\u0442\u044C \u0443\u0440\u043E\u0432\u0435\u043D\u044C \u043C\u0430\u0441\u0442\u0435\u0440\u0441\u0442\u0432\u0430 (\u043A\u0430\u043A \u0447\u0435\u043B\u043E\u0432\u0435\u043A \u0441\u043F\u0440\u0430\u0432\u043B\u044F\u0435\u0442\u0441\u044F \u0441\u043E \u0441\u0432\u043E\u0438\u043C\u0438 \u043F\u0440\u044F\u043C\u044B\u043C\u0438 \u043E\u0431\u044F\u0437\u0430\u043D\u043D\u043E\u0441\u0442\u044F\u043C\u0438).</p>\n<p>\u0414\u043B\u044F \u043C\u0435\u043D\u0435\u0434\u0436\u0435\u0440\u0430 - \u043A\u0430\u043A \u043E\u043D \u0432\u0435\u0434\u0435\u0442 \u043F\u0440\u043E\u0435\u043A\u0442 \u0432 \u0434\u0436\u0438\u0440\u0435, \u043D\u0430\u0441\u043A\u043E\u043B\u044C\u043A\u043E \u0441\u043B\u0435\u0434\u0443\u0435\u0442 \u043C\u0435\u0442\u043E\u0434\u043E\u043B\u043E\u0433\u0438\u044F\u043C, \u043D\u0430\u0441\u043A\u043E\u043B\u044C\u043A\u043E \u044F\u0441\u043D\u043E \u0441\u0442\u0430\u0432\u0438\u0442 \u0437\u0430\u0434\u0430\u0447\u0438 \u0438 \u0440\u0435\u0448\u0430\u0435\u0442 \u0441\u043F\u043E\u0440\u043D\u044B\u0435 \u0441\u0438\u0442\u0443\u0430\u0446\u0438\u0438.</p>\n<p>\u0414\u043B\u044F \u0442\u0435\u0441\u0442\u0438\u0440\u043E\u0432\u0449\u0438\u043A\u0430 - \u043D\u0430\u0441\u043A\u043E\u043B\u044C\u043A\u043E \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u0435\u043D\u043D\u043E \u0442\u0435\u0441\u0442\u0438\u0440\u0443\u0435\u0442, \u043A\u0430\u043A\u0438\u043C\u0438 \u0442\u0435\u0445\u043D\u0438\u043A\u0430\u043C\u0438 \u0442\u0435\u0441\u0442\u0438\u0440\u043E\u0432\u0430\u043D\u0438\u044F \u0432\u043B\u0430\u0434\u0435\u0435\u0442, \u043A\u0430\u043A \u0432\u0435\u0434\u0435\u0442 \u0434\u043E\u043A\u0443\u043C\u0435\u043D\u0442\u0430\u0446\u0438\u044E.\n</p>\n<p>\u0414\u043B\u044F \u0434\u0438\u0437\u0430\u0439\u043D\u0435\u0440\u0430 - \u043A\u0430\u0447\u0435\u0441\u0442\u0432\u043E  \u043C\u0430\u043A\u0435\u0442\u0430 (\u0441\u0435\u0442\u043A\u0430, \u0434\u0435\u0442\u0430\u043B\u0438\u0437\u0430\u0446\u0438\u044F \u044D\u043A\u0440\u0430\u043D\u043E\u0432), \u043E\u0442\u0441\u0443\u0442\u0441\u0442\u0432\u0438\u0435 \u043B\u043E\u0433\u0438\u0447\u0435\u0441\u043A\u0438\u0445 \u043E\u0448\u0438\u0431\u043E\u043A \u0438 \u0442.\u0434. \u0412\u0430\u0436\u043D\u043E \u0443\u0447\u0438\u0442\u044B\u0432\u0430\u0442\u044C \u0441\u043F\u0435\u0446\u0438\u0430\u043B\u0438\u0437\u0430\u0446\u0438\u044E.</p>\n<p><b>\u041E\u0447\u0435\u043D\u044C \u0432\u0430\u0436\u043D\u043E, \u0447\u0442\u043E \u043A\u0430\u0436\u0434\u044B\u0439 \u043E\u0446\u0435\u043D\u0438\u0432\u0430\u0435\u0442 \u0442\u043E\u043B\u044C\u043A\u043E \u0442\u0435\u0445, \u043A\u043E\u0433\u043E \u043E\u043D \u043C\u043E\u0436\u0435\u0442 \u043E\u0446\u0435\u043D\u0438\u0442\u044C \u0438 \u0442\u043E\u043B\u044C\u043A\u043E \u043F\u043E \u0442\u0435\u043C \u043A\u0440\u0438\u0442\u0435\u0440\u0438\u044F\u043C, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043E\u043D \u043C\u043E\u0436\u0435\u0442 \u043E\u0446\u0435\u043D\u0438\u0442\u044C!\n</b></p>\n<p>\u0415\u0441\u043B\u0438 \u0441 \u043A\u0435\u043C-\u0442\u043E \u0432\u044B \u0440\u0430\u0431\u043E\u0442\u0430\u043B\u0438 \u0432\u043C\u0435\u0441\u0442\u0435 \u0434\u0430\u0432\u043D\u043E \u0438 \u0441 \u0442\u0435\u0445 \u043F\u043E\u0440 \u0443\u0440\u043E\u0432\u0435\u043D\u044C \u0447\u0435\u043B\u043E\u0432\u0435\u043A\u0430 \u044F\u0432\u043D\u043E \u0438\u0437\u043C\u0435\u043D\u0438\u043B\u0441\u044F, \u043E\u0442 \u043E\u0446\u0435\u043D\u043A\u0438 \u043B\u0443\u0447\u0448\u0435 \u0432\u043E\u0437\u0434\u0435\u0440\u0436\u0430\u0442\u044C\u0441\u044F.\n</p>\n\n<hr />\n<h3>\u041F\u0440\u043E\u0446\u0435\u0441\u0441 \u043E\u0446\u0435\u043D\u043A\u0438:</h3>\n<p>\u041E\u043F\u0440\u0435\u0434\u0435\u043B\u044F\u0435\u043C\u0441\u044F \u0441\u043E \u0441\u043F\u0438\u0441\u043A\u043E\u043C \u043B\u044E\u0434\u0435\u0439, \u043A\u043E\u0442\u043E\u0440\u044B\u0445 \u0432\u044B \u043C\u043E\u0436\u0435\u0442\u0435 \u043E\u0446\u0435\u043D\u0438\u0442\u044C \u043F\u043E \u043A\u0430\u043A\u0438\u043C-\u043B\u0438\u0431\u043E \u043A\u0440\u0438\u0442\u0435\u0440\u0438\u044F\u043C.</p>\n<p>\u0417\u0430\u0442\u0435\u043C \u0432\u0430\u043C \u0432\u044B\u0434\u0430\u044E\u0442\u0441\u044F \u0441\u0442\u0438\u043A\u0435\u0440\u044B \u0441 \u0438\u043C\u0435\u043D\u0430\u043C\u0438 \u044D\u0442\u0438\u0445 \u043B\u044E\u0434\u0435\u0439. \u0422\u0430\u043A\u0436\u0435 \u0435\u0441\u0442\u044C \u043F\u0440\u043E\u0431\u043A\u043E\u0432\u0430\u044F \u0434\u043E\u0441\u043A\u0430, \u0440\u0430\u0437\u0431\u0438\u0442\u0430\u044F \u043D\u0430 \u0448\u0435\u0441\u0442\u044C \u0448\u043A\u0430\u043B, \u043E\u0431\u043E\u0437\u043D\u0430\u0447\u0430\u044E\u0449\u0438\u0445 \u043E\u0446\u0435\u043D\u0438\u0432\u0430\u0435\u043C\u044B\u0435 \u043D\u0430\u0432\u044B\u043A\u0438.</p>\n<p>\u0421\u0442\u0438\u043A\u0435\u0440\u044B \u0440\u0430\u0441\u043F\u0440\u0435\u0434\u0435\u043B\u044F\u044E\u0442\u0441\u044F \u043F\u043E \u0448\u043A\u0430\u043B\u0430\u043C \u0441\u043B\u0435\u0432\u0430 \u043D\u0430\u043F\u0440\u0430\u0432\u043E (\u0441\u043B\u0435\u0432\u0430 \u043D\u0438\u0437\u043A\u0438\u0439 \u0443\u0440\u043E\u0432\u0435\u043D\u044C \u0432\u043B\u0430\u0434\u0435\u043D\u0438\u044F \u043D\u0430\u0432\u044B\u043A\u043E\u043C, \u0441\u043F\u0440\u0430\u0432\u0430 - \u0432\u044B\u0441\u043E\u043A\u0438\u0439). \u0428\u043A\u0430\u043B\u044B \u043D\u0435 \u0438\u043C\u0435\u044E\u0442 \u044F\u0432\u043D\u043E\u0439 \u0447\u0438\u0441\u043B\u043E\u0432\u043E\u0439 \u0433\u0440\u0430\u0434\u0430\u0446\u0438\u0438. \u041E\u043F\u0440\u0435\u0434\u0435\u043B\u0435\u043D\u0438\u0435 \u043D\u0438\u0437\u043A\u043E\u0433\u043E/\u0432\u044B\u0441\u043E\u043A\u043E\u0433\u043E \u0443\u0440\u043E\u0432\u043D\u044F \u043E\u0441\u043D\u043E\u0432\u044B\u0432\u0430\u0435\u0442\u0441\u044F \u0442\u043E\u043B\u044C\u043A\u043E \u043D\u0430 \u0432\u0430\u0448\u0435\u043C \u043B\u0438\u0447\u043D\u043E\u043C \u0432\u043E\u0441\u043F\u0440\u0438\u044F\u0442\u0438\u0438 \u0432\u044B\u0441\u043E\u043A\u043E\u0433\u043E (\u043B\u0438\u0431\u043E \u043D\u0438\u0437\u043A\u043E\u0433\u043E) \u0443\u0440\u043E\u0432\u043D\u044F. \u041C\u044B \u043D\u0435 \u043F\u0440\u0435\u0434\u043B\u0430\u0433\u0430\u0435\u043C \u043F\u0440\u0438\u043D\u044F\u0442\u044C \u043A\u0430\u043A\u043E\u0433\u043E-\u0442\u043E \u043A\u043E\u043D\u043A\u0440\u0435\u0442\u043D\u043E\u0433\u043E \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A\u0430 \u0437\u0430 \u044D\u0442\u0430\u043B\u043E\u043D.</p>\n<p>\u041D\u0438\u0436\u0435 \u043C\u043E\u0436\u043D\u043E \u043F\u043E\u0441\u043C\u043E\u0442\u0440\u0435\u0442\u044C \u043F\u0440\u0438\u043C\u0435\u0440 \u0442\u043E\u0433\u043E, \u043A\u0430\u043A \u044D\u0442\u043E \u0432\u044B\u0433\u043B\u044F\u0434\u0438\u0442 \u043D\u0430 \u0431\u0443\u043C\u0430\u0433\u0435 (\u0443 \u043D\u0430\u0441 \u0431\u0443\u0434\u0435\u0442 \u0442\u043E \u0436\u0435 \u0441\u0430\u043C\u043E\u0435, \u0442\u043E\u043B\u044C\u043A\u043E \u0432\u043C\u0435\u0441\u0442\u043E \u0431\u0443\u043C\u0430\u0433\u0438 \u043C\u044B \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u0443\u0435\u043C \u0434\u043E\u0441\u043A\u0443).</p>\n<p>\n  <img src=\"http://lh3.googleusercontent.com/HZPzUEDP0ZcUr-d5A5C9_RgtZAxijkyKUVyvKl3lKh8HQuKmWplzi-OxBIUPXBU5f38E594ia8hPlcO6NPHGZgjXTlxdv4JoqY7x8ClLMydtfWJmog5BHEIi5bDETIMm2bI0fSCX\" alt=\"example\">\n</p>\n<p>\u0422\u0430\u043A\u0438\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u043C, \u0438\u0441\u0445\u043E\u0434\u044F \u0438\u0437 \u0440\u0438\u0441\u0443\u043D\u043A\u0430, \u0441\u0430\u043C\u044B\u0439 \u0432\u044B\u0441\u043E\u043A\u0438\u0439 \u0443\u0440\u043E\u0432\u0435\u043D\u044C Hard Programming Skills \u0443 \u041E\u043A\u0441\u0430\u043D\u044B, \u0441\u0430\u043C\u044B\u0439 \u043D\u0438\u0437\u043A\u0438\u0439 \u0443 \u041F\u0435\u0442\u0438. Hard Operation Skills \u0443 \u0441\u0430\u043C\u044B\u0439 \u0432\u044B\u0441\u043E\u043A\u0438\u0439 \u0443 \u041A\u0430\u0442\u0438, \u0441\u0430\u043C\u044B\u0439 \u043D\u0438\u0437\u043A\u0438\u0439 \u0442\u0430\u043A \u0436\u0435 \u0443 \u041F\u0435\u0442\u0438.</p>\n<p>\u0417\u0430\u0442\u0435\u043C \u0441\u043B\u0435\u0434\u0443\u0435\u0442 \u043E\u0431\u0449\u0435\u043D\u0438\u0435 \u0441 \u0432\u0435\u0434\u0443\u0449\u0438\u043C \u0438 HR. \u0412\u0430\u0441 \u043F\u043E\u043F\u0440\u043E\u0441\u044F\u0442 \u043E\u0431\u044A\u044F\u0441\u043D\u0438\u0442\u044C \u043F\u043E\u0447\u0435\u043C\u0443 \u0432\u0430\u0448\u0430 \u0448\u043A\u0430\u043B\u0430 \u0432\u044B\u0433\u043B\u044F\u0434\u0438\u0442 \u0442\u0430\u043A\u0438\u043C \u043E\u0431\u0440\u0430\u0437\u043E\u043C: \u043F\u043E\u0447\u0435\u043C\u0443 \u043D\u0430 \u0432\u0430\u0448 \u0432\u0437\u0433\u043B\u044F\u0434 \u0443 \u041F\u0435\u0442\u0438 \u0441\u0430\u043C\u044B\u0435 \u0441\u043B\u0430\u0431\u044B\u0435 \u0441\u043A\u0438\u043B\u044B, \u0438 \u0442.\u0434.  \u0414\u043E\u0431\u0430\u0432\u043B\u0435\u043D\u0438\u0435 \u043A\u043E\u043C\u043C\u0435\u043D\u0442\u0430\u0440\u0438\u0435\u0432 \u043F\u0440\u0438\u0432\u0435\u0442\u0441\u0442\u0432\u0443\u0435\u0442\u0441\u044F.</p>\n\n<hr />\n<h3>\u0412\u0430\u0436\u043D\u043E:</h3>\n<p>\u0415\u0441\u043B\u0438 \u0432\u044B \u0434\u0430\u0435\u0442\u0435 \u043A\u043E\u043C\u043C\u0435\u043D\u0442\u0430\u0440\u0438\u0439 \u043F\u043E \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A\u0443, \u043F\u043E\u0441\u0442\u0430\u0440\u0430\u0439\u0442\u0435\u0441\u044C \u0434\u0435\u043B\u0430\u0442\u044C \u044D\u0442\u043E \u0431\u043E\u043B\u0435\u0435 \u0440\u0430\u0437\u0432\u0435\u0440\u043D\u0443\u0442\u043E \u0438 \u0430\u0440\u0433\u0443\u043C\u0435\u043D\u0442\u0438\u0440\u043E\u0432\u0430\u043D\u043E, \u0430 \u043D\u0435 \u043F\u0440\u043E\u0441\u0442\u043E \"\u041D\u0443\u0443\u0443 \u043A\u0430\u043A-\u0442\u043E \u0442\u0430\u043A\". \u042D\u0442\u043E \u043E\u0447\u0435\u043D\u044C \u043F\u043E\u043C\u043E\u0436\u0435\u0442 \u0432\u0430\u043C \u0438 \u0432\u0430\u0448\u0438\u043C \u043A\u043E\u043B\u043B\u0435\u0433\u0430\u043C, \u043F\u043E\u043D\u044F\u0442\u044C, \u0447\u0442\u043E \u043E\u043D\u0438 \u0434\u0435\u043B\u0430\u044E\u0442 \u0445\u043E\u0440\u043E\u0448\u043E, \u0430 \u0447\u0442\u043E \u043F\u043B\u043E\u0445\u043E.</p>\n\n<hr />\n\n<p>\u0422\u0430\u043A\u0436\u0435 \u0432\u044B \u043E\u0431\u0441\u0443\u0436\u0434\u0430\u0435\u0442\u0435 \u0441 \u043A\u0443\u0440\u0430\u0442\u043E\u0440\u043E\u043C \u0432\u0430\u0448\u0438 \u0434\u043E\u0441\u0442\u0438\u0436\u0435\u043D\u0438\u044F\u0445, \u043E\u0436\u0438\u0434\u0430\u043D\u0438\u044F, \u043F\u043E\u0436\u0435\u043B\u0430\u043D\u0438\u044F \u043F\u043E \u043F\u0440\u043E\u0446\u0435\u0441\u0441\u0430\u043C \u0440\u0430\u0431\u043E\u0442\u044B.</p>\n<p>\u0415\u0441\u0442\u044C \u043D\u0435\u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0432\u0435\u0449\u0438, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u0434\u043E\u043B\u0436\u043D\u044B \u0443\u0447\u0438\u0442\u044B\u0432\u0430\u0442\u044C\u0441\u044F \u043E\u0442\u0434\u0435\u043B\u044C\u043D\u043E. \u041D\u0430\u043F\u0440\u0438\u043C\u0435\u0440, \u0430\u043A\u0442\u0438\u0432\u043D\u043E\u0435 \u0443\u0447\u0430\u0441\u0442\u0438\u0435 \u0447\u0435\u043B\u043E\u0432\u0435\u043A\u0430 \u0432 \u0440\u0430\u0437\u0432\u0438\u0442\u0438\u0438 \u043A\u043E\u043C\u043F\u0430\u043D\u0438\u0438, \u0432 \u043E\u043F\u0435\u043D\u0441\u043E\u0440\u0441 \u043F\u0440\u043E\u0435\u043A\u0442\u0430\u0445 \u0431\u0443\u0434\u0435\u0442 \u043F\u043E\u043E\u0449\u0440\u044F\u0442\u044C\u0441\u044F. \u041B\u0438\u0431\u043E \u0436\u0435 \u043D\u0430\u043E\u0431\u043E\u0440\u043E\u0442, \u0431\u0443\u0434\u0435\u0442 \u0443\u0447\u0438\u0442\u044B\u0432\u0430\u0435\u0442\u0441\u044F \u0440\u0435\u0433\u0443\u043B\u044F\u0440\u043D\u043E\u0435 \u043F\u0440\u043E\u0432\u0430\u043B\u0438\u0432\u0430\u043D\u0438\u0435 \u0441\u0440\u043E\u043A\u043E\u0432 \u0438\u043B\u0438 \u0438\u043D\u043E\u0435 \u043D\u0435\u0433\u0430\u0442\u0438\u0432\u043D\u043E\u0435 \u0432\u043E\u0437\u0434\u0435\u0439\u0441\u0442\u0432\u0438\u0435 \u043D\u0430 \u0440\u0430\u0431\u043E\u0447\u0438\u0439 \u043F\u0440\u043E\u0446\u0435\u0441\u0441 \u0438 \u0440\u0435\u0437\u0443\u043B\u044C\u0442\u0430\u0442. \u0423 \u043A\u0443\u0440\u0430\u0442\u043E\u0440\u0430 \u0435\u0441\u0442\u044C \u043E\u0434\u0438\u043D \u0431\u0430\u043B\u043B, \u043A\u043E\u0442\u043E\u0440\u044B\u0439 \u043E\u043D \u043C\u043E\u0436\u0435\u0442 \u043F\u0440\u0438\u0431\u0430\u0432\u0438\u0442\u044C \u043A \u0438\u0442\u043E\u0433\u043E\u0432\u043E\u0439 \u043E\u0446\u0435\u043D\u043A\u0435, \u0443\u0431\u0440\u0430\u0442\u044C \u0438\u043B\u0438 \u043D\u0435 \u0438\u0441\u043F\u043E\u043B\u044C\u0437\u043E\u0432\u0430\u0442\u044C \u0432\u043E\u0432\u0441\u0435.</p>\n<p>\u041A\u0430\u0436\u0434\u043E\u043C\u0443 \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A\u0443 \u0431\u0443\u0434\u0443\u0442 \u043F\u043E\u043A\u0430\u0437\u0430\u043D\u044B \u0435\u0433\u043E \u0440\u0435\u0437\u0443\u043B\u044C\u0442\u0430\u0442\u044B \u0438 \u0432\u0430\u0436\u043D\u044B\u0435 \u0437\u0430\u043C\u0435\u0447\u0430\u043D\u0438\u044F, \u043A\u043E\u0442\u043E\u0440\u044B\u0435 \u043C\u044B \u043F\u043E\u043B\u0443\u0447\u0438\u043C \u043E\u0442 \u0432\u0430\u0448\u0438\u0445 \u043A\u043E\u043B\u043B\u0435\u0433. \u0420\u0435\u0437\u0443\u043B\u044C\u0442\u0430\u0442\u044B \u0434\u0440\u0443\u0433\u0438\u0445 \u0441\u043E\u0442\u0440\u0443\u0434\u043D\u0438\u043A\u043E\u0432 \u043F\u0440\u0435\u0434\u0430\u0432\u0430\u0442\u044C\u0441\u044F \u043E\u0433\u043B\u0430\u0441\u043A\u0435 \u043D\u0435 \u0431\u0443\u0434\u0443\u0442.</p>\n";
	
	exports.default = rules;

